import { Component, OnInit, Inject } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import * as moment from 'moment';
import * as _ from 'lodash';

import { IListItemsPage } from 'src/app/services/list-items-service';

import { Employee } from 'src/app/staff/services/employees/employee';
import { EmployeesFactory } from 'src/app/staff/services/employees/employees.factory';

export interface Birthday {
  birthday: string;
  birthmonth: string;
  employees: Employee[];
}

@Component({
  selector: 'app-birthdays-page',
  templateUrl: './birthdays-page.component.html',
  styleUrls: ['./birthdays-page.component.scss']
})
export class BirthdaysPageComponent implements OnInit {
  private pages: IListItemsPage<Employee>[] = [];
  private index = 0;
  public employees: Employee[] = [];
  public birthdays: Birthday[] = [];
  public hasPrev = false;
  public hasNext = false;
  public month: number;
  public selectedDay: number;
  public currentDay: number;
  public months: string[];
  public days = [];

  constructor(
    private route: ActivatedRoute,
    @Inject('EmployeesFactory') private employeesFactory: EmployeesFactory
  ) {
    this.months = Array.apply(0, Array(12)).map((v, i: number) => moment().month(i).format('MMMM'));
    this.days = Array.apply(0, Array(moment().daysInMonth())).map((v, i: number) => i + 1);
    this.month = new Date().getMonth();
    this.currentDay = new Date().getDate();
  }

  ngOnInit() {
    this.pages.push(this.route.snapshot.data.birthdays);
    this.employees = this.route.snapshot.data.birthdays.items;
    this.birthdays = this.getBirthdaysForEmployees(this.employees);
    this.hasNext = this.route.snapshot.data.birthdays.hasNext;
  }

  public getBirthdaysPagetByMonth(month?: number) {
    this.month = month == null ? this.month : month ;
    this.index = 0;
    this.employees = [];
    this.pages = [];
    this.birthdays = [];
    this.hasPrev = false;
    this.hasNext = false;
    this.employeesFactory.getPagedByMonthBirthday(this.month + 1)
      .subscribe((page: IListItemsPage<Employee>) => {
        this.employees = page.items;
        this.birthdays = this.getBirthdaysForEmployees(this.employees);
        this.hasNext = page.hasNext;
        this.pages.push(page);
      });
    this.getDaysInMonth(month);
  }

  public getDaysInMonth(month: number) {
    const thisYear = new Date().getFullYear();
    const date = new Date(thisYear, month, 1);
    this.days = [];
    while (date.getMonth() === month) {
      this.days.push(new Date(date).getDate());
      date.setDate(date.getDate() + 1);
    }
    return this.days;
  }

  public getBirthdaysPagetByDayOfMonth(day?: number) {
    if (day === this.currentDay) {
      this.month = new Date().getMonth();
    }
    this.selectedDay = day == null ? this.selectedDay : day ;
    this.index = 0;
    this.employees = [];
    this.pages = [];
    this.birthdays = [];
    this.hasPrev = false;
    this.hasNext = false;
    this.employeesFactory.getPagedByRange(this.month + 1, this.month + 1,
      this.selectedDay, this.selectedDay)
      .subscribe((page: IListItemsPage<Employee>) => {
        this.employees = page.items;
        this.birthdays = this.getBirthdaysForEmployees(this.employees);
        this.hasNext = page.hasNext;
        this.pages.push(page);
      });
  }

  public getNext(): void {
    if (this.hasNext) {
      if (this.index === (this.pages.length - 1)) {
        this.pages[this.index].getNext()
          .subscribe((page: IListItemsPage<Employee>) => {
            this.index++;
            this.employees = page.items;
            this.birthdays = this.getBirthdaysForEmployees(page.items);
            this.hasNext = page.hasNext;
            this.hasPrev = true;
            this.pages.push(page);
          });
      } else {
        this.index++;
        this.employees = this.pages[this.index].items;
        this.birthdays = this.getBirthdaysForEmployees(this.employees);
        this.hasNext = this.pages[this.index].hasNext;
        this.hasPrev = true;
      }
    }
  }

  public getPrev(): void {
    if (this.hasPrev) {
      this.index--;
      this.employees = this.pages[this.index].items;
      this.birthdays = this.getBirthdaysForEmployees(this.employees);
      this.hasNext = true;
      this.hasPrev = this.index > 0;
    }
  }

  private getBirthdaysForEmployees(employees: Employee[]): Birthday[] {
    const birthdays: Birthday[] = [];
    const grouped = _.groupBy(employees, (employee: Employee) => employee.birthdayTitleRelative);
    for (const group in grouped) {
      if (grouped.hasOwnProperty(group)) {
        const dates: string[] = group.split(' ');
        if (dates.length === 2) {
          birthdays.push({ birthday: dates[0], birthmonth: dates[1], employees: grouped[group] });
        }
      }
    }
    return birthdays;
  }
}
