import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { BirthdaysPageComponent } from './components/birthdays-page/birthdays-page.component';
// import { MsalGuard } from '@azure/msal-angular';
import { StaffModule } from '../staff/staff.module';
import { BirthdaysResolver } from './services/employees/birthdays.resolver';

const routes: Routes = [
  {
    path: ':code',
    component: BirthdaysPageComponent,
    resolve: { birthdays: BirthdaysResolver },
    // canActivate : [MsalGuard],
    data: { animation: 'fadeAnimation', title: 'Дни рождения' }
  }
];

@NgModule({
    imports: [ RouterModule.forChild(routes), StaffModule ],
    providers: [ BirthdaysResolver ],
    exports: [RouterModule]
})
export class BirthdaysRoutingModule { }
